.top {
  width: 100%;
  position: relative;
  height: 750px; }
  @media (max-width: 992px) {
    .top {
      height: 550px; } }
  .top_title {
    color: #FFFFFF;
    text-align: center;
    position: absolute;
    top: 300px;
    left: 30%; }
    @media (max-width: 1200px) {
      .top_title {
        left: 20%; } }
    @media (max-width: 992px) {
      .top_title {
        top: 100px; } }
    .top_title_wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start; }
    .top_title_1 {
      font-size: 90px;
      line-height: 105px;
      white-space: nowrap;
      font-weight: bold;
      text-transform: uppercase; }
      @media (max-width: 900px) {
        .top_title_1 {
          font-size: 50px; } }
      @media (max-width: 600px) {
        .top_title_1 {
          font-size: 30px; } }
    .top_title_1-white {
      font-size: 90px;
      line-height: 105px;
      white-space: nowrap;
      font-weight: bold;
      text-transform: uppercase; }
      .top_title_1-white span {
        opacity: 0.2; }
    .top_title_2 {
      font-size: 30px;
      line-height: 35px;
      font-weight: 300;
      text-transform: uppercase; }
      @media (max-width: 900px) {
        .top_title_2 {
          font-size: 20px; } }
      @media (max-width: 600px) {
        .top_title_2 {
          font-size: 15px; } }
    .top_title_bottom {
      display: flex; }
  .top_social {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 5%;
    top: 45%;
    height: 100%; }
    @media (max-width: 800px) {
      .top_social {
        display: none; } }
    .top_social__block {
      z-index: 10; }

.top_img {
  margin-left: 20px;
  width: calc(100% - 20px); }
  .top_img__block_1 {
    width: 40%;
    border-left: 2px solid #959595;
    border-bottom: 2px solid #959595;
    box-sizing: border-box;
    border-radius: 2px;
    margin-top: 20px;
    padding: 12px 0 33px 30px; }
    .top_img__block_1 img {
      width: 100%;
      height: 100%;
      object-fit: cover; }

.top_img__block_2 {
  position: absolute;
  width: 40%;
  left: 40%;
  margin: 20px 0 0 70px; }
  .top_img__block_2_wrapper {
    border: 1px solid #FFFFFF;
    padding: 39px 36px 0 36px; }
    @media (max-width: 900px) {
      .top_img__block_2_wrapper {
        padding: 20px 20px 0 20px; } }
    @media (max-width: 600px) {
      .top_img__block_2_wrapper {
        padding: 10px 10px 0 10px; } }
  .top_img__block_2 img {
    margin-top: 15px;
    width: 100%;
    height: 100%;
    padding-bottom: 30px;
    object-fit: cover;
    border-bottom: 2px solid #959595; }

.top_img__block_3 {
  position: absolute;
  width: 50%;
  left: 45%;
  margin: 20px 0 0 70px; }
  .top_img__block_3_wrapper {
    border: 1px solid #FFFFFF;
    padding: 39px 36px 0 36px;
    text-align: start; }
    @media (max-width: 900px) {
      .top_img__block_3_wrapper {
        padding: 20px 20px 0 20px; } }
    @media (max-width: 600px) {
      .top_img__block_3_wrapper {
        padding: 10px 10px 0 10px; } }
    .top_img__block_3_wrapper .img1 {
      margin-top: 15px;
      width: 80%;
      object-fit: cover; }
    .top_img__block_3_wrapper .img2 {
      margin-top: 15px;
      width: 100%;
      object-fit: cover; }

.block__text {
  max-width: 448px;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #FFFFFF;
  margin: 0 auto;
  font-weight: 300; }
  @media (max-width: 900px) {
    .block__text {
      font-size: 15px; } }
  .block__text_2 {
    margin-top: 30px;
    width: 100%;
    font-size: 20px;
    line-height: 23px;
    color: #1F1B20;
    padding: 0 36px; }
    @media (max-width: 900px) {
      .block__text_2 {
        font-size: 15px; } }

.btn_wrapper {
  margin-top: 40px; }
