.header {
  position: fixed;
  top: 0;
  background: #1F1B20;
  z-index: 100; }
  @media (max-width: 992px) {
    .header {
      padding: 0; } }
  .header_main {
    display: flex;
    justify-content: space-around;
    align-items: center; }
    @media (max-width: 992px) {
      .header_main {
        display: none;
        position: fixed;
        flex-direction: column;
        top: 104px;
        background: black;
        width: 90%;
        height: 100%; }
        .header_main.active {
          display: flex; } }
  .header_mob {
    display: none; }
    @media (max-width: 992px) {
      .header_mob {
        display: flex; } }
  .header_sections a {
    color: #828282;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    padding-bottom: 10px;
    font-weight: 300; }
    @media (max-width: 992px) {
      .header_sections a {
        color: #F2C94C; } }
    .header_sections a:hover {
      color: #FFFFFF;
      border-bottom: 1px solid #F2C94C; }
  .header_logo {
    padding: 10px 0 12px 0; }
    @media (max-width: 992px) {
      .header_logo {
        margin: 0 auto; } }
  .header_hamburger {
    overflow: hidden;
    position: relative;
    margin-left: 30px;
    top: 30px;
    width: 50px;
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .header_line {
    transition: all .2s;
    position: absolute;
    width: 100%;
    height: 3px;
    border-radius: 18px;
    margin: 4px 0;
    cursor: pointer;
    background: #F2C94C; }
    .header_line_1 {
      top: 10px; }
    .header_line_2 {
      top: 22px; }
    .header_line_3 {
      top: 34px; }

.header.active .header_line_1 {
  top: 22px;
  transform: rotate(45deg); }

.header.active .header_line_2 {
  left: -100px; }

.header.active .header_line_3 {
  top: 22px;
  transform: rotate(-45deg); }
