.footer_title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 100px 0 27px 0; }
  .footer_title__text {
    font-size: 30px;
    line-height: 35px;
    text-transform: uppercase;
    color: #1F1B20;
    font-weight: 500;
    text-align: center;
    margin-right: 35px; }

.footer__img {
  display: flex; }
  .footer__img img {
    width: 20%; }

.footer_bottom {
  background: #1F1B20;
  display: flex; }
  @media (max-width: 992px) {
    .footer_bottom {
      flex-direction: column; } }
  .footer_bottom .google_maps {
    width: 50%; }
    @media (max-width: 992px) {
      .footer_bottom .google_maps {
        width: 100%;
        height: 400px; } }
    .footer_bottom .google_maps iframe {
      width: 100%;
      height: 100%; }
  .footer_bottom_right {
    width: 50%; }
    @media (max-width: 992px) {
      .footer_bottom_right {
        width: 100%; } }
    .footer_bottom_right .contacts_info {
      padding: 10% 20%; }
      .footer_bottom_right .contacts_info_section {
        display: flex;
        align-items: center; }
        .footer_bottom_right .contacts_info_section img {
          margin-right: 30px; }
        .footer_bottom_right .contacts_info_section__text {
          font-size: 18px;
          line-height: 21px;
          color: #FFFFFF;
          font-weight: 300; }
    .footer_bottom_right .contact_us {
      text-align: center; }
    .footer_bottom_right .sections_container {
      display: flex;
      justify-content: space-around;
      margin-top: 40px; }
      @media (max-width: 600px) {
        .footer_bottom_right .sections_container {
          flex-direction: column;
          align-items: center; } }
      .footer_bottom_right .sections_container__item {
        margin-right: 10px; }
        .footer_bottom_right .sections_container__item a {
          color: #828282;
          font-size: 15px;
          line-height: 18px;
          text-transform: uppercase;
          padding-bottom: 10px;
          font-weight: 300; }
    .footer_bottom_right .sw-logo {
      text-align: center;
      margin-top: 40px; }
